import React from "react";
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
import { InputDesktop } from "@alfalab/core-components/input/desktop";
import { ButtonDesktop } from "@alfalab/core-components/button/desktop";

import { useForm } from "../../hooks/useForm";
import BackLink from "../../components/BackLink/BackLink";
import Helper from "../../components/Helper/Helper";

import styles from "./Login.module.scss";

const Login = ({
  onLogin,
  isLoggedEmployee = false,
  isLoggedLeader = false,
}) => {
  const navigate = useNavigate();
  const { values, resetForm, handleChange, errors, setErrors, isValid } =
    useForm();
  const [isFormDisabled, setIsFormDisabled] = React.useState(false);

  React.useEffect(() => {
    if (isLoggedEmployee) {
      navigate("/employee", { replace: true });
    }
    if (isLoggedLeader) {
      navigate("/leader", { replace: true });
    }
  });

  const handleSubmit = (evt) => {
    evt.preventDefault();
    /* onLogin() */
    setIsFormDisabled(true);
    alert(`: ${values.name} : ${values.password}`); //
    resetForm();
    setIsFormDisabled(false);
  };

  const handleCancel = () => {
    resetForm();
    setErrors({});
  };

  return (
    <main className={styles.login}>
      <BackLink text="" link={"/"} onShowPopup={() => resetForm()} />
      <h1>   </h1>
      <div className={styles.content}>
        <form
          onSubmit={handleSubmit}
          id="login-form"
          className={styles.form}
          noValidate
        >
          <h3></h3>
          <InputDesktop
            onChange={handleChange}
            required
            minLength="2"
            maxLength="30"
            name="name"
            label=""
            block
            type="text"
            size="m"
            autoComplete="off"
            disabled={isFormDisabled}
            error={errors.name}
            value={values.name || ""}
          />
          <InputDesktop
            onChange={handleChange}
            required
            minLength="6"
            name="password"
            label=""
            block
            autoComplete="off"
            type="password"
            size="m"
            disabled={isFormDisabled}
            error={errors.password}
            value={values.password || ""}
          />
          <div className={styles.button__field}>
            <ButtonDesktop
              className={styles.button}
              type="submit"
              disabled={!isValid || isFormDisabled}
              view="accent"
            >
              
            </ButtonDesktop>
            <ButtonDesktop
              onClick={handleCancel}
              className={styles.button}
              view="tertiary"
              disabled={(!values.name && !values.password) || isFormDisabled}
            >
              
            </ButtonDesktop>
          </div>
        </form>

        <Helper
          title="     MVP"
          advices="         ,   
            ,    "
        />
      </div>

      <div className={styles.wrapperLink}>
        <Link className={styles.login__link} to={"/leader"}>
          leader
        </Link>
        <Link className={styles.login__link} to={"/employee"}>
          employee
        </Link>
        <Link className={styles.login__link} to={"/create-ipr"}>
          create-ipr
        </Link>
        <Link className={styles.login__link} to={"/management"}>
          management
        </Link>
      </div>
    </main>
  );
};

export default Login;
