import React, { useState } from "react";
import { useLocation } from "react-router-dom";
import { Button } from "@alfalab/core-components/button";
import { Input } from "@alfalab/core-components/input";
import { Textarea } from "@alfalab/core-components/textarea";
import { UniversalDateInput } from "@alfalab/core-components/universal-date-input";
import ProgressBar from "../ProgressBar/ProgressBar";

import ConfirmPopup from "../ConfirmPopup/ConfirmPopup";
import Helper from "../Helper/Helper";

import styles from "./IPRCard.module.scss";

function IPRCard({ onClickCancel, showPopup, onClickOk, onShowPopup, title }) {
  const [dataValue, setDataValue] = useState("");
  const [titleValue, setTitleValue] = useState("");
  const [descriptionValue, setDescriptionValue] = useState("");


  const { pathname } = useLocation();

  const handleChangeDataInput = (_, { value }) => {
    setDataValue(value);
    //setTitleValue(value);
    //setDescriptionValue(value);
  };

  return (
    <>
      <article className={styles.card}>
        <h3>{title}</h3>
        <div className={styles.container}>
          <form id="ipr-form" className={styles.form}>
            <Input
              name="title-IPR"
              label=" "
              block
              type="text"
              size="m"
              value={titleValue}
              //onChange={}
            />
            <Input
              name="description-IPR"
              label=""
              block
              type="text"
              size="m"
              value={descriptionValue}
              //onChange={}
            />
            <Textarea
              label={""}
              autosize={false}
              minRows={3}
              block={true}
            />
            <UniversalDateInput
              block={false}
              view="date"
              label="e "
              labelView="inner"
              size="m"
              className={styles.input_date}
              picker={true}
              value={dataValue}
              onChange={handleChangeDataInput}
              clear={false}
              onClear={(e) => {
                e.stopPropagation();
                setDataValue("");
              }}
            />
          </form>
          {pathname === '/create-ipr' ? <Helper
            title="  "
            advices={["  ", "    "]}
          /> : <ProgressBar />}
        </div>
        {pathname === '/create-ipr' 
        ? <Button
          type="submit"
          form="ipr-form"
          className={styles.button}
          view="accent"
          name="create"
          disabled
        >
          
        </Button>
        :
        <div style={{width: 382, justifyContent: 'space-between', display: 'flex'}}>
          <Button
          view="primary"
          size="m"
          name="save-changes"
          className={styles.button_save}
          >
             
          </Button>
          <Button
          type="button"
          view="tertiary"
          size="m"
          name="cancel"
          className={styles.button_cancel}
          onClick={onShowPopup}
          >
            
          </Button>
        </div>
        }
        
      </article>
      {pathname === '/create-ipr' 
      ? <ConfirmPopup
        showPopup={showPopup}
        onClickCancel={onClickCancel}
        onClickOk={onClickOk}
        title=" ,     ?"
        description="    ."
        leftButtonText=""
        rightButtonText=""
      />
      : <ConfirmPopup
      showPopup={showPopup}
      onClickCancel={onClickCancel}
      onClickOk={onClickOk}
      title=" ,    ?"
      description="    ."
      leftButtonText=""
      rightButtonText=""
    />
      }
      
    </>
  );
}

export default IPRCard;
